@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "NewAthena";
  src: local("NewAthena"),
    url(/epidoc-viewer/static/media/newathu5_7.60c94226.woff) format("woff");
  font-weight: normal;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.leiden-transcription hi[rend="superscript"] {
    vertical-align: super;
  }
  
  .leiden-transcription note { 
    visibility: hidden;
  }
  
  .leiden-transcription { 
    counter-reset: footnotecounter;
    line-height: 1.5; 
  }
  
  .leiden-transcription note:after { 
     
    visibility: visible;
  
    counter-increment: footnotecounter; /* Add 1 to footnote */
    content: counter(footnotecounter);
    font-size: smaller; 
    margin-left: .1em;
    vertical-align: super; /* sets the footnote above the baseline */
  }
  
  .leiden-numbering  {
    margin-right: 1em; 
  }

  .leiden-transcription {
    font-family: NewAthena;
    white-space: pre-wrap;
  }

